<template>
  <div class="center">
    <Card style="width:320px">
        <div style="text-align:center">
          <img src="./../assets/img/avatar.png" alt="Licoy">
          <br><br>
            <h3>憧憬Licoy / Licoy</h3>
            <br>
            <div>
              <Tooltip content="Github" placement="bottom">
              <Button type="primary" @click="send" data-text="https://github.com/Licoy" shape="circle" icon="social-github"></Button>
              </Tooltip>
            </div>
        </div>
    </Card>
  </div>
</template>

<script>
import Card from 'iview/src/components/card';
import Tooltip from 'iview/src/components/tooltip';
import Button from 'iview/src/components/button';
export default {
  components:{
      Card,Tooltip,Button
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods:{
    send(event){
      window.location = event.target.dataset.text
    }
  }
}
</script>

<style scoped>
.center{
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  overflow: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
</style>



